/* СТИЛИ ДЛЯ СТРАНИЦЫ С АВТОРИЗАЦИЕЙ */
/* БЛОК авторизации */
.auth {
  display: flex;
  justify-content: center;
  margin-top: 150px;
}

/* ЭЛЕМЕНТ подложки */
.auth__wrap {
  display: inline-flex;
  align-items: flex-end;
  padding: 30px;
}

/* ЭЛЕМЕНТ логотипа */
.auth__logo {
  padding-right: 70px;
}
.auth__logo img {
  width: 100%;
}

/* ЭЛЕМЕНТ подложки */
.auth__entry {
  width: 300px;
  padding: 20px 30px;
  box-shadow: 0 1px 1px 0 rgba(60, 75, 100, 0.14), 0 2px 1px -1px rgba(60, 75, 100, 0.12), 0 1px 3px 0 rgba(60, 75, 100, 0.2);
  border-radius: 4px;
  background: #FFFFFF;
}

/* ЭЛЕМЕНТ с кнопками контроля */
.auth__control {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* ПЕРЕМЕННЫЕ */
/* БЛОК с кнопки */
.btn {
  display: inline-flex;
  cursor: pointer;
  color: #3c4b64;
  justify-content: center;
  align-items: center;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0 15px;
  height: 38px;
  font-size: 14px;
  border-radius: 4px;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.btn:disabled {
  opacity: 0.65;
  cursor: default;
}
.btn:not(:disabled):not(.disabled):active {
  box-shadow: inset 1px 1px 5px rgba(0, 0, 0, 0.2);
}

/* МОДИФИКАТОР для кнопки - маленький */
.btn--small {
  height: 30px;
  font-size: 13px;
  padding: 0 8px;
}

/* МОДИФИКАТОР для кнопки - синий */
.btn--primary {
  color: #FFFFFF;
  background-color: #007bff;
  border-color: #007bff;
}
.no-touchevents .btn--primary:not(:disabled):not(.disabled):hover {
  color: #FFFFFF;
  background-color: #0062cc;
  border-color: #0062cc;
}
.btn--primary:not(:disabled):not(.disabled):active, .btn--primary:not(:disabled):not(.disabled):focus {
  color: #FFFFFF;
  background-color: #0062cc;
  border-color: #005cbf;
}

/* МОДИФИКАТОР для кнопки - красный */
.btn--danger {
  color: #FFFFFF;
  background-color: #dc3545;
  border-color: #dc3545;
}
.no-touchevents .btn--danger:not(:disabled):not(.disabled):hover {
  color: #FFFFFF;
  background-color: #c82333;
  border-color: #bd2130;
}
.btn--danger:not(:disabled):not(.disabled).active, .btn--danger:not(:disabled):not(.disabled):focus {
  color: #FFFFFF;
  background-color: #bd2130;
  border-color: #b21f2d;
}

/* МОДИФИКАТОР для кнопки - зеленый */
.btn--success {
  color: #FFFFFF;
  background-color: #28a745;
  border-color: #28a745;
}
.btn--success:disabled {
  opacity: 0.65;
  cursor: default;
}
.no-touchevents .btn--success:not(:disabled):not(.disabled):hover {
  color: #FFFFFF;
  background-color: #149331;
  border-color: #149331;
}
.btn--success:not(:disabled):not(.disabled).active, .btn--success:not(:disabled):not(.disabled):focus {
  color: #FFFFFF;
  background-color: #149331;
  border-color: #149331;
}

/* МОДИФИКАТОР для кнопки - серый */
.btn--light {
  color: #3c4b64;
  background-color: #f8f9fa;
  border-color: #f8f9fa;
}
.btn--light:disabled {
  opacity: 0.65;
  cursor: default;
}
.no-touchevents .btn--light:not(:disabled):not(.disabled):hover {
  background-color: #e9eaeb;
  border-color: #f9fafb;
}
.btn--light:not(:disabled):not(.disabled).active, .btn--light:not(:disabled):not(.disabled):focus {
  background-color: #f9fafb;
  border-color: #f9fafb;
}

/* МОДИФИКАТОР для кнопки - темно-серый */
.btn--secondary {
  color: #FFFFFF;
  background-color: #6c757d;
  border-color: #6c757d;
}
.btn--secondary:disabled {
  opacity: 0.65;
  cursor: default;
}
.no-touchevents .btn--secondary:not(:disabled):not(.disabled):hover {
  background-color: #565e64;
  border-color: #51585e;
}
.btn--secondary:not(:disabled):not(.disabled).active, .btn--secondary:not(:disabled):not(.disabled):focus {
  background-color: #5c636a;
  border-color: #565e64;
}

/* МОДИФИКАТОР для кнопки - желтый */
.btn--warning {
  color: #3c4b64;
  background-color: #ffed4a;
  border-color: #ffed4a;
}
.btn--warning:disabled {
  opacity: 0.65;
  cursor: default;
}
.no-touchevents .btn--warning:not(:disabled):not(.disabled):hover {
  background-color: #f9e744;
  border-color: #f9e744;
}
.btn--warning:not(:disabled):not(.disabled).active, .btn--warning:not(:disabled):not(.disabled):focus {
  background-color: #f9e744;
  border-color: #f9e744;
}

.btn-outline-primary {
  font-weight: 500;
  border: 2px solid;
  transition: opacity 0.08s;
}
.no-touchevents .btn-outline-primary:hover {
  color: #3490dc;
  background-color: transparent;
  border-color: #005cbf;
  opacity: 0.7;
}

/* БЛОКИ общей разметки */
*,
*::before,
*::after {
  box-sizing: border-box;
}

[v-cloak] > * {
  display: none;
}

body {
  color: #3c4b64;
  background-color: #f8fafc;
  font-family: "Nunito", sans-serif;
  outline: none;
  overflow: auto;
}

a,
button {
  outline: none;
  text-decoration: none;
}

h1, h2, h3, h4, h5 {
  margin: 0;
  font-weight: 400;
  color: #3c4b64;
}

h1 {
  font-size: 24px;
}

h2 {
  font-size: 22px;
}

h3 {
  font-size: 20px;
  font-weight: bold;
}

h4 {
  font-size: 18px;
  line-height: 22px;
}

